html {
  --heading-size: 25px;
  --heading-base-width: 20rem;
  --font-icon-size: 72px;
  --font-greeting-size: 5.5rem;
  --font-name-size: 3rem;
  --font-status-size: 1.5rem;
  --img-size: 15rem;
  cursor: pointer;
}


.section-download-btn {
  /* position: absolute;
  top: 0;
  right: 0; */
  width: 12rem;
  padding: 1.5rem;
  color: #FCFFF5;
  -ms-flex-item-align: center;
      align-self: center;
  border: 2px solid;
}

.header-hobby {
  padding: .5rem;
  margin: .5rem;
  font-size: 1.5rem;
}

.cursive {
  font-family: 'Leckerli One', cursive;
}

.section-download-btn:hover {
  background-color: #FCFFF5;
  color: #222831;
}

a:hover {
  text-decoration: none;
  color: inherit;
}

.section-header {
  height: 80vh;
  width: 100%;
  background-color: #222831;
  color: #FCFFF5;
  display: grid;
  place-content: center;
  grid-template-rows: 60vh 1fr;
}

.header-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
  place-content: space-evenly;
}

.header-contact{
  color: #D1DBBD;
  /* position: absolute; */

  /* grid-column: 1 / -1; */
  grid-row: 3;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  /* height: 10rem; */
}

.remove-default {
  text-decoration: none;
  color: #D1DBBD;
}

.header-contact-icon {
  font-size: var(--font-icon-size);
}

.highlight {
  font-size: 1.3rem;
  text-decoration: none;
  display: inline;
  background-color: #ecd018;
  padding: 6px;
  border-radius: 4px;
}

.highlight-word {
  color: #D1DBBD;
}

.header-photo {
  width: 100%;
  height: 100%;
  -webkit-clip-path: circle(40% at 50% 50%);
          clip-path: circle(40% at 50% 50%);
  -o-object-fit: cover;
     object-fit: cover;
}

.section-white-banner {
  background-color: #3E606F;
  border-bottom: 5px solid var(--highlight-border-color);
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #4876EB;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.header-img {
  width: var(--img-size);
  height: var(--img-size);
}
.home-photo-img {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  /* transform: translateY(-115px) scale(1.1); */
  border-radius: 16rem;
  border: 5px solid #D1DBBD;
}

.header-greating {
  font-size: var(--font-greeting-size);
  font-weight: 300;
  line-height: 1.2;

}

.header-name {
  font-size: var(--font-name-size);
}

.header-status {
  font-size: var(--font-status-size)
}

.border-right  {
  border-right: 2px solid;
}

@media only screen and (max-width: 600px) {
  html {
    --heading-size: 25px;
    --heading-base-width: 20rem;
    --font-icon-size: 48px;
    --font-greeting-size: 2.2rem;
    --font-name-size: 1.25rem;
    --font-status-size: 1rem;
    --img-size: 10rem;
  }
  /* .home-photo-img {
     transform: translateY(-90px) scale(1.5);
  } */
  .highlight {
    padding: .4rem;
  }
  .section-download-btn {
    width: 5rem;
    padding: 1rem;
  }
  .mobile-hide {
    display: none;
  }
}


.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  place-content: center;
}


